body {
    margin: 0;
    padding: 0;
	font-family: 'Lato', Calibri, Arial, sans-serif;
	color: #ffe8e7;
	background: #E96D65;
	font-weight: 300;
	overflow-x: hidden;
    font-size: 5px;
}

.App {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

#hero {
    position: fixed;
    height: auto;
    margin: 0;
    padding: 5vw;
    z-index: 20;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    bottom: 0;
}

#hero img {
    width: 50%;
    height: 50%;
    position: fixed;
    top: 10vh;
    left: -10vw;
}
.nl-phase {
    -webkit-transition: all 500ms ease-in-out;
    -o-transition: all 500ms ease-in-out;
    transition: all 500ms ease-in-out;
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0)
}
.hidden {
    opacity: 0;
    max-height: 0;
    -webkit-transform: translateY(200%);
        -ms-transform: translateY(200%);
            transform: translateY(200%)
}

.flex {
    display: -ms-flexbox;
    display: flex;
}


.flex > * {
    -ms-flex: 1 1;
        flex: 1 1;
}

.flex h1 {
    font-size: 4em;
    padding: 0 5vw;
    margin: auto
}

.flex form {
    font-size: 4em;
    margin: auto
}

.flex input {
    color: white;
}

@media only screen and (max-device-width: 480px) {
    .flex {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .flex h1 {
        font-size: 3em;
    }
    .flex form {
        font-size: 4em;
    }
}




